.centered-flexbox {
    display: flex;
    justify-content: center;
    align-items: center;
}

.education-img {
    max-width: 100%;
}

.margin-top {
    margin-top: 20px;
}

.padding-around {
    padding: 20px;
}
.centerize {
    align-content: center;
}

.card {
    width: 98%;
    height: auto;
}

.infoCard {
    width: 100%;
    margin: 10px;
}

.bpCard {
    width: 100%;
    margin: 15px 0;
    float: left;
    min-width : 450px;
}

.urineTestCard {
    margin: 15px 0;
    float: right;
    min-width : 450px;
    width: 100%;
}

.bpField {
    margin: auto;
    width : 350px;
    text-align: right;
}

.symptomCard {
    width: 100%;
    min-width: 450px;
    float: left;
}

.flexBox {
    display: flex;
    flex-direction: row;
    justify-content: center;
    width: 95%;
}

.contentRight {
    float: right;
    margin: 20px;
}

.leftContainer {
    float: left;
    min-width: 450px;
    width: 49%;
}

.rightContainer {
    float: right;
    min-width: 450px;
    width: 49%;
}

input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
  -webkit-appearance: none; 
  margin: 0; 
}
.headerSize {
    font-size: 40px;
}

.statisticBox {
    padding-left: 5%;
    padding-right: 5%;
}

.underlineBlue {
    border-bottom: 2px solid #4bc0c0;
}

.underlinePurple {
    border-bottom: 2px solid #9400D3;
}

.underlineOrange {
    border-bottom: 2px solid #FF7F50;
}

.underlineBlack {
    border-bottom: 2px solid #15152B;
}

.virticalWritting {
    width: 100px;
    margin: 0 auto;
    line-height: 16px;
}

.statSubBox {
    width: 200px;
    padding: 5px 0 5px 10px;
    border: 1px solid rgb(211, 205, 205);
    border-radius: 7px;
    box-shadow: 3px 1px rgb(211, 205, 205); 
}

.chartBox {
    width: 75%;
}

.setBottomHeight {
    margin-bottom: -10px;
}

.centerize {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.loginOverlay {
    background-color: lightblue;
    height: calc(100vh - 70px);
    /* width: 100vw; */
    display: flex;
    justify-content: center;
    align-items: center;
    margin: -24px;
}

.loginContainer {
    /* background-color: white; */
}

.loginFormWrapper {
    margin-top: 20px;
}

img.logo {
    height: 100px;
}

div.loginFormWrapper > form > label > input {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
    font-size: 15px;
    padding-left: 5px;
}
.chatContainer {
    height: 100%;
    width: 25%;
    border-left: 1px solid black;
    background-color: white;
}

.chatInput {
    height: 95px;
    width: 100%;
    padding-left: 10px;
    padding-right: 10px;
    box-shadow: 0px -1px 100px 1px rgba(50, 50, 50, 0.75);
}

.chatHistory {
    /* height: calc(100% - 95px - 50px); */
    height: calc(100% - 95px);
    overflow-y: scroll;
    border-bottom: 2px solid black;
}

.chatMessage {
    display: flex;
    /* justify-content: space-between; */
    margin-top: 5px;
    padding-bottom: 5px;    
    border-bottom-style: solid;
    border-width: 1px;
    border-color: black;
}

.chatAvatar {
    height: 50px;
    margin-left: 10px;
    width: 50px;
    border: 1px solid;
    border-radius: 10px;
}

.chatMessage > div {
    word-wrap: break-word;
    max-width: calc(100% - 70px);
    /* padding:10px; */
}

::-webkit-scrollbar {
    width: 0px;  /* remove scrollbar space */
    background: transparent;  /* optional: just make scrollbar invisible */
}

button.chatSubmitButton {
    position: absolute;
    right: 15px;
    padding: 0;
    font-size: 10px;
    margin-right: 0;
}

div.chatInfoContainer {
    width: 100%;
    height: 50px;
    background-color: #428FE6;
    /* position: absolute; */
    display:flex;
    justify-content: center;
    align-items: center;
}

span.chatInfoName {
    /* position: absolute;
    left: 25.24%;
    right: 32.38%;
    top: 38.89%;
    bottom: 34.44%; */

    font-family: Roboto;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    font-size: 20px;
    color: white;
}

.chatContent {
    display: flex;
    flex-direction: column;
    margin: 0 10px;
}

.messageUserName {
    font-weight: bold;
}
.session {
    height: calc(100vh - 150px);
    width: 100%;
    background-color: lightpink;
    border: 2px solid black;
}

.session-ui {
    height: 100%;
    width: 100%;
}

.topSection {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
}

.streamContainer {
    width: 75%;
    height: 100%;
}

.remoteStream {
    height: 100%;
    width: 100%;
    background-color: black;
    box-sizing: border-box;
    border: 1px black solid;
}

.localStream {
    height: 200px;
    width: 300px;
    position: relative;
    bottom: 225px;
    right: calc(-100% + 300px + 25px);
    background-color: black;
    border-radius: 15px;
    border: 1px white solid;
}

#localStream > video {
    height: 100%;
    width: 100%;
}

#remoteStream > video {
    height: 100%;
    width: 100%;
}

.infoDiv {
    padding-left: 10px;
    position: absolute;
}

.infoDiv > p {
    font-size: 20px;
    color: white;
    text-align: left;
}

:root {
  --theme_color: #8fe0e7;
  --button_color: #84ced4;
  --button_hover_color: #4893ba;
}

html {
  font-size: 100%;
}

body {
  height: 100%;
  width: 100%;
  font-size: 1rem;
  line-height: 1.5;
  box-sizing: border-box;
}

main {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Helvetica, Arial, sans-serif;
}

.logout:hover {
  opacity: 0.5;
}

.loginWrapper {
    display: flex;
    flex-direction: row;
    justify-content: center;
    height: calc(86vh - 54px);
    /* height: 100vh; */
    width: 100vw;
    justify-content: center;
    align-items: center; 
    position: relative;   
}

.loginWrapper > * {
    min-height: auto !important;
}

.subWrapper {
    width: 45%;
    margin: 0;
}

.imgStyle {
    height: 100%;
    width: 100%;
    position: relative;
    left: -25%;
}

.inputStyle {
    width: 65%;
    font-size: 20px;
    border-bottom: solid 1px #000;
    position: relative;
    left: 2%;
}

input {
  border: 0;
}

.content-box-signup {
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 100%;
  width: 45%;
  min-width: 500px;
  margin-right: 15%;
  margin-bottom: 25%;
  padding: 10px;
  position: relative;
  box-shadow: 2px 5px 5px #888888;
  margin: auto;
}

.content-box h1 {
  font-weight: 400;
  font-size: 3em;
  margin: 0.2em 0 0.1em;
}

input::-webkit-input-placeholder {
    color: rgb(112, 106, 106);
}

input::-ms-input-placeholder {
    color: rgb(112, 106, 106);
}

input::placeholder {
    color: rgb(112, 106, 106);
}


button:disabled {
  opacity: 0.5;
}

.contant-submit {
  background-color: #84ced4;
  background-color: var(--button_color);
  width: 20%;
  height: 37px;
  font-size: 21px;
  border-radius: 5px;
  margin: 7%;
  border: 0;
  position: relative;
  right: -230px;
}

.contant-submit:hover {
  background-color: #4893ba;
  background-color: var(--button_hover_color);
}

.white {
  color: #fff;
}

.center {
  text-align: center;
  display: block;
  justify-content: center;
  align-items: center;
  margin: auto;
}

.appIcon {
  width: 100px;
  padding: 20px;
}

.sidebarIcon {
  width: 75%;
}

* {
  font-family: 'IBM Plex Sans', sans-serif;
}

.formError {
  font-size: 12px;
  color: #e53e3e;
  width: 400px;
  margin-top: 0.25rem;
}

